<template>
  <main class="content container">

    <div class="content__top content__top--catalog">
      <h1 class="content__title">
        
      </h1>
      <span class="content__info">
        152 
      </span>
    </div>

    <div class="content__catalog">

      <ProductFilter :price-from.sync="filterPriceFrom"
                     :price-to.sync="filterPriceTo"
                     :colors="colors"
                     :color.sync="filterColor"
                     :category-id.sync="filterCategoryId"
      />


      <section class="catalog">
        <div v-if="productsLoading"> ...
        </div>

        <Preloader  v-if="productsLoading" />
        <div v-if="productsLoadingFailed">
              
          <button @click.prevent="loadProducts"> </button>
        </div>
        <ProductList :products="products"/>
        <BasePagination v-model="page" :page="page" :count-el="countProducts"
                        :per-page="productsPerPage"/>

      </section>

    </div>

  </main>

</template>

<script>
import ProductList from '@/components/ProductList';
import ProductFilter from '@/components/ProductFilter';
import BasePagination from '@/components/BasePagination';
import axios from 'axios';
import { API_BASE_URL } from '@/config';
import Preloader from '@/components/preloaders/PreLoader';


export default {
  data() {
    return {
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: 0,
      filterColor: '',
      colors: [],

      page: 1,
      productsPerPage: 3,

      // .. - -     
      productsData: 0,
      productsLoading: false,
      productsLoadingFailed: false
    };
  },
  components: {
    BasePagination,
    ProductList,
    ProductFilter,
    Preloader,
  },
  // loadProducts    1
  created() {
    // this.colorsToLowerCase()
    this.loadProducts();
    this.loadColors();
  },
  methods: {
    loadColors() {
      axios.get(API_BASE_URL + '/api/colors')
        .then(response => {
          this.colors = response.data.items;
        });
    },
    loadProducts() {
      this.productsLoading = true;
      clearTimeout(this.loadProductsTimer);
      this.loadProductsTimer = setTimeout(() => {
        axios.get(API_BASE_URL + `/api/products`, {
          params: {
            page: this.page,
            limit: this.productsPerPage,
            categoryId: this.filterCategoryId,
            minPrice: this.filterPriceFrom,
            maxPrice: this.filterPriceTo,
            colorId: this.filterColor
          }
        })
          .then(response => {
            this.productsData = response.data;
          })
          .catch(() => this.productsLoadingFailed = true)
          .then(() => this.productsLoading = false);
      }, 0);
    },
  },
  watch: {
    page() {
      this.loadProducts();
    },
    filterPriceFrom() {
      this.loadProducts();
    },
    filterPriceTo() {
      this.loadProducts();
    },
    filterCategoryId() {
      this.loadProducts();
    },
    filterColor() {
      this.loadProducts();
    }
  },
  computed: {
    products() {
      return this.productsData
        // ..  
        ? this.productsData.items.map(product => {
          return {
            ...product,
            image: product.image.file.url
          };
        })
        : [];
    },
    countProducts() {
      return this.productsData ? this.productsData.pagination.total : 0;
    },
  }
};
</script>

<style scoped>
  .catalog {
    position: relative;
  }
</style>
